import React, { CSSProperties, useRef, useState } from 'react';
import mergeImages from 'merge-images';
import FavoriteIcon from '@mui/icons-material/Favorite';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import FileDownloadIcon from '@mui/icons-material/FileDownload';
import ErrorOutlineIcon from '@mui/icons-material/ErrorOutline';
import { IconButton, Tooltip } from '@mui/material';

type ExtendAttr = {
  stack: number;
  attr: string;
}

type AvatarProps = {
  id: string | number
  attrs: string[]
  extendAttrs?: ExtendAttr[]
  className?: string
  style?: CSSProperties
  collected?: boolean
  decrepted?: boolean
  onCollectedChange?: (collected: boolean) => void
}

const Avatar = (props: AvatarProps) => {
  const {attrs, extendAttrs, id, collected, onCollectedChange, decrepted, ...rest} = props;
  const downloadRef = useRef<HTMLAnchorElement>(null)
  const [showAction, setShowAction] = useState(false);
  const [downloading, setDownloading] = useState(false);

  const handleDownload = async () => {
    setDownloading(true);
    try {
      const b64 = await mergeImages(props.attrs);
      downloadRef.current!.href = b64;
      downloadRef.current!.click();
    } catch (error) {
      console.error(error);
    } finally {
      setDownloading(false);
    }
  }

  const handleSwitchCollect = () => {
    onCollectedChange && onCollectedChange(!collected);
  }

  const rawAttrs: ExtendAttr[] = attrs.map((attr, index) => ({
    stack: index,
    attr,
  }));
  
  const rawExtendAttrs: ExtendAttr[] = extendAttrs ? extendAttrs.map(({ stack, attr }) => ({
    stack: stack + 0.1,
    attr,
  })) : [];
  const fullAttrs = [...rawAttrs, ...rawExtendAttrs].sort((a, b) => a.stack - b.stack);

  return (
    <div 
      className="avatar" 
      onMouseOver={() => setShowAction(true)} 
      onMouseLeave={() => setShowAction(false)}
      {...rest}
    >
      <div className="collect">
        <IconButton onClick={handleSwitchCollect}>
          {collected ? (
            <FavoriteIcon style={{color: "#ef5350"}}/>
          ): (
            <FavoriteBorderIcon style={{color: "#616161"}}/>
          )}
        </IconButton>
      </div>
      
      {decrepted &&
      <div className="version">
        <Tooltip title="旧版组合">
          <IconButton>
            <ErrorOutlineIcon style={{color: "#ff9800"}}/>
          </IconButton>
        </Tooltip>
      </div>}
      
      <div className="action" style={{display: showAction ? "block" : "none"}}>
        <span style={{color: "#0277bd", marginRight: 2}}>{`#${id}`}</span>
        <IconButton disabled={downloading} onClick={handleDownload}>
          <FileDownloadIcon style={{color: "#616161"}}/>
        </IconButton>
      </div>
      {fullAttrs.map(({ attr }, j) => (
        attr !== "none" && 
        <div style={{zIndex: j + 1}} key={j} className="attr">
          <img src={attr} alt="" loading='lazy' />
        </div>
      ))}
      <a
        style={{ display: "none" }}
        download={`${id}.png`}
        href={"/"}
        ref={downloadRef}
      >download</a>
    </div>
  )
}

export default Avatar;