import React, { useEffect, useRef, useState } from "react";
import RefreshIcon from "@mui/icons-material/Refresh";
import firebase from 'firebase/compat/app';
import { getDatabase, ref, get, set, onValue, remove } from "firebase/database";
import OnlinePredictionIcon from "@mui/icons-material/OnlinePrediction";
import OfflineBoltIcon from "@mui/icons-material/OfflineBolt";
import BugReportIcon from '@mui/icons-material/BugReport';
import AddIcon from "@mui/icons-material/Add";
import ImageIcon from "@mui/icons-material/Image";
import { blue } from "@mui/material/colors";

import "./App.css";
import MultipleSelect from "./components/MultipleSelect";
import {
  Avatar,
  Box,
  Button,
  Checkbox,
  Container,
  Dialog,
  DialogTitle,
  FormControlLabel,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Stack,
} from "@mui/material";

import materials, { Attribute, ExtendAttribute, Material } from "./materials";
import ImageList, { ImageListInstance } from "./components/ImageList";
import { LoadingButton } from "@mui/lab";
import DirectorySelect from "./components/DirectorySelect";
import AttributeFilter from "./components/AttributeFilter";
import FirebaseAuthDialog from "./components/FirebaseAuthDialog";

function App() {
  // const firebaseConfig = {
  //   apiKey: process.env.REACT_APP_FIREBASE_APIKEY,
  //   authDomain: "avatar-gen-13f3a.firebaseapp.com",
  //   databaseURL: process.env.REACT_APP_FIREBASE_DATABASE,
  //   projectId: "avatar-gen-13f3a",
  //   storageBucket: "avatar-gen-13f3a.appspot.com",
  //   messagingSenderId: "543498960517",
  //   appId: "1:543498960517:web:b1fddaab59df404727f3ec",
  // };

  // const firebaseApp = initializeApp(firebaseConfig);

  const database = getDatabase(firebase.app());

  const [showMaterialSelect, setShowMaterialSelect] = useState(false);
  const [material, setMaterial] = useState<Material | undefined>(undefined);
  const [filter, setFilter] = useState<Record<number, number[]>>({});
  const [extendFilter, setExtendFilter] = useState<Record<number, number[]>>({});
  const [collects, setCollects] = useState<Set<string>>(new Set());
  const [rawCollects, setRawCollects] = useState<Set<number>>(new Set());
  const [isOnline, setIsOnline] = useState(false);
  const [isSyncing, setIsSyncing] = useState(false);
  const imageListRef = useRef<ImageListInstance>(null);
  const [totalCount, setTotalCount] = useState(0);
  const [filteredCount, setFilteredCount] = useState(0);
  const [onlyCollect, setOnlyCollect] = useState(false);
  const [showDecrepted, setShowDecrepted] = useState(false);
  const actionsContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!material || material.isLocal) return;
    const collectName = material.extendName ? `${material.name}/extends/${material.extendName}` : material.name;
    const collectsRef = ref(database, `materials/${collectName}/collects`);
    const unscribe = onValue(collectsRef, (snapshot) => {
      const data: Record<number, boolean> | null = snapshot.val();
      if (data) {
        setCollects(new Set(Object.keys(data)));
      }
    });

    const connectedRef = ref(database, ".info/connected");
    const connectUnscribe = onValue(connectedRef, (snap) => {
      if (snap.val() === true) {
        setIsOnline(true);
      } else {
        setIsOnline(false);
      }
    });

    return () => {
      unscribe();
      connectUnscribe();
    };
  }, [material]);

  const setCollect = async (
    material: Material,
    id: number|string,
    collected: boolean
  ) => {
    setIsSyncing(true);
    const collectName = material.extendName ? `${material.name}/extends/${material.extendName}` : material.name;
    const collectRef = ref(database, `materials/${collectName}/collects/${id}`);
    try {
      if (collected) {
        await set(collectRef, true);
      } else {
        await remove(collectRef);
      }
    } finally {
      setIsSyncing(false);
    }
  };

  const handleFilter = (index: number, value: number[]) => {
    setFilter({
      ...filter,
      [index]: value,
    });
  };

  const handleResetFilter = () => {
    setFilter({});
  };

  const handleExtendFilter = (index: number, value: number[]) => {
    setExtendFilter({
      ...filter,
      [index]: value,
    });
  };

  const handleResetExtendFilter = () => {
    setExtendFilter({});
  };

  const handleSelectMaterial = (material: Material) => {
    setShowMaterialSelect(false);
    setMaterial(material);
    if (material.extendName) {
      const collectsRef = ref(database, `materials/${material.name}/collects`);
      get(collectsRef).then((snapshot) => {
        const data = snapshot.val();
        setRawCollects(new Set(Object.keys(data).map((id) => parseInt(id))));
      });
    }
    setFilter({});
  };

  const handleAddMaterial = async (files: FileList) => {
    const imgs = Array.from(files)
      .filter((file) => file.type === "image/png")
      .sort((a, b) => a.webkitRelativePath.localeCompare(b.webkitRelativePath));
    
    const material: Material = {
      name: "",
      isLocal: true,
      attributes: [],
    };
    for (let i = 0; i < imgs.length; i++) {
      const ps = imgs[i].webkitRelativePath.split("/");
      if (ps.length !== 3) continue;
      if (!material.name) {
        material.name = ps[0];
      }
      const attrName = ps[1].replace(/[0-9]+/g, "");
      const valueName = ps[2].replace(".png", "");
      const valueUrl = URL.createObjectURL(imgs[i]);
      if (material.attributes.length > 0 && material.attributes[material.attributes.length - 1].name === attrName) {
        material.attributes[material.attributes.length - 1].values.push({
          name: valueName,
          img: valueUrl,
        });
      } else {
        material.attributes.push({
          name: attrName,
          values: [
            {
              name: valueName,
              img: valueUrl,
            },
          ],
        });
      }
    }

    handleSelectMaterial(material);
  };

  const handleCollect = (
    id: number | string,
    collected: boolean
  ) => {
    if (!material) return;
    setCollects((collects) => {
      const newCollects = new Set(collects);
      if (collected) {
        newCollects.add(`${id}`);
      } else {
        newCollects.delete(`${id}`);
      }
      return newCollects;
    });
    if (!material.isLocal) {
      setCollect(material, id, collected);
    }
  };

  useEffect(() => {
    setTotalCount(imageListRef.current?.count() ?? 0);
    setFilteredCount(imageListRef.current?.filteredCount() ?? 0);
  }, [material, collects, rawCollects, filter, onlyCollect]);

  const actionsHeight = actionsContainerRef.current?.clientHeight || 86;

  const materialName = material ? `${material.name}${material.extendName || ""}` : "";

  return (
    <div style={{ height: "100%", overflow: "hidden" }}>
      <Container
        ref={actionsContainerRef}
        fixed
        style={{ marginTop: 10, width: "100%", maxWidth: "100%" }}
      >
        <Stack spacing={1}>
          <Stack direction="row" spacing={1}>
            <Button
              variant="outlined"
              disabled={!!material}
              onClick={() => !material && setShowMaterialSelect(true)}
            >
              {materialName || "选择素材包"}
            </Button>
            <Dialog
              open={showMaterialSelect}
              onClose={() => setShowMaterialSelect(false)}
            >
              <DialogTitle>选择素材包</DialogTitle>
              <List sx={{ pt: 0 }}>
                {materials.map((material, index) => (
                  <ListItem
                    button
                    onClick={() => handleSelectMaterial(material)}
                    key={index}
                  >
                    <ListItemAvatar>
                      <Avatar sx={{ bgcolor: blue[100], color: blue[600] }}>
                        <ImageIcon />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText primary={`${material.name}${material.extendName || ""}`} />
                  </ListItem>
                ))}
                <DirectorySelect onSelect={handleAddMaterial}>
                  <ListItem button>
                    <ListItemAvatar>
                      <Avatar>
                        <AddIcon />
                      </Avatar>
                    </ListItemAvatar>
                    <ListItemText primary="添加素材包" />
                  </ListItem>
                </DirectorySelect>
              </List>
            </Dialog>
            {material && (
              <div
                style={{ lineHeight: "38px" }}
              >{`总数：${filteredCount} / ${totalCount}`}</div>
            )}

            {material && (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={onlyCollect}
                    onChange={(_, checked) => setOnlyCollect(checked)}
                  />
                }
                label={`只看收藏(${collects.size})`}
              />
            )}

            {material && (
              <FormControlLabel
                control={
                  <Checkbox
                    checked={showDecrepted}
                    onChange={(_, checked) => setShowDecrepted(checked)}
                  />
                }
                label="显示旧版标识"
              />
            )}

            <FirebaseAuthDialog />

            {material && (
              <div style={{ lineHeight: "38px" }}>
                <IconButton>
                  {material.isLocal ? (
                    <BugReportIcon fontSize="small" color="warning" />
                  ) : (
                    isOnline ? (
                      <OnlinePredictionIcon fontSize="small" color="success" />
                    ) : (
                      <OfflineBoltIcon fontSize="small" color="error" />
                    )
                  )}
                </IconButton>
                {isSyncing && <LoadingButton loading />}
              </div>
            )}
          </Stack>
          {material && 
            <AttributeFilter 
              attributes={material.attributes} 
              filter={filter}
              onFilter={handleFilter}
              onResetFilter={handleResetFilter}
            />
          }
          {material && material.extendAttributes &&
            <AttributeFilter 
              attributes={material.extendAttributes?.map(item => item.attribute)} 
              filter={extendFilter}
              onFilter={handleExtendFilter}
              onResetFilter={handleResetExtendFilter}
            />
          }
        </Stack>
      </Container>
      {material && (
        <ImageList
          ref={imageListRef}
          material={material}
          filter={filter}
          extendFilter={extendFilter}
          marginTop={actionsHeight + 20}
          onCollect={handleCollect}
          showDecrepted={showDecrepted}
          collects={collects}
          rawCollects={rawCollects}
          onlyCollect={onlyCollect}
        />
      )}
    </div>
  );
}

export default App;
