import m4 from "./加密骑士0325收藏扩展";

export type AttributeValue = {
  name: string;
  img: string;
  isOld?: boolean;
}

export type Attribute = {
  name: string;
  values: AttributeValue[];
}

export type Material = {
  name: string;
  isLocal?: boolean;
  attributes: Attribute[];
  extendName?: string;
  extendAttributes?: ExtendAttribute[];
}

export type ExtendAttribute = {
  stack: number;
  attribute: Attribute;
}

