import React, { useEffect, useRef } from "react";
import { styled } from "@mui/material/styles";

export type DirectoryInputProps = {
  children: React.ReactNode;
  onSelect?: (files: FileList) => void;
}

const Input = styled("input")({
  display: "none",
});

const DirectorySelect = (props: DirectoryInputProps) => {
  const { children, onSelect } = props;
  const folderSelectRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    const folderSelect = folderSelectRef.current;
    if (folderSelect) {
      folderSelect.setAttribute("directory", "true");
      folderSelect.setAttribute("webkitdirectory", "true");
    }
  }, [folderSelectRef]);

  const handleSelectFiles = (files: FileList) => {
    onSelect && onSelect(files);
  }

  return (
    <label htmlFor="contained-button-file">
      <Input
        id="contained-button-file"
        ref={folderSelectRef}
        onChange={(e) =>
          e.target.files && handleSelectFiles(e.target.files)
        }
        type="file"
      />
      {children}
    </label>
  )
}

export default DirectorySelect;