import React from "react";
import {
  Box,
  Button,
  Stack,
} from "@mui/material";
import RefreshIcon from "@mui/icons-material/Refresh";
import { Attribute } from "../../materials";
import MultipleSelect from "../MultipleSelect";

export type AttributeFilterProps = {
  attributes: Attribute[];
  onFilter: (index: number, value: number[]) => void;
  onResetFilter: () => void;
  filter: Record<number, number[]>;
};

const AttributeFilter = (props: AttributeFilterProps) => {
  const { attributes, filter, onFilter, onResetFilter } = props;
  return (
    <Box className="selections">
      <Stack direction="row" spacing={1}>
        {attributes.map((attr, i) => (
          <MultipleSelect
            key={i}
            label={attr.name}
            options={attr.values.map((v, i) => ({
              label: v.name,
              value: i,
            }))}
            value={filter[i]}
            onChange={(value) => onFilter(i, value)}
          />
        ))}

        <Button
          variant="outlined"
          color="error"
          onClick={() => onResetFilter()}
          startIcon={<RefreshIcon />}
          size="small"
        >
          重置
        </Button>
      </Stack>
    </Box>
  );
};

export default AttributeFilter;