import React, { forwardRef, Ref, useImperativeHandle, useMemo } from "react";
import { FixedSizeGrid } from "react-window";
import AutoSizer from "react-virtualized-auto-sizer";

import { Material } from "../../materials";
import Avatar from "../Avatar";

export type ImageListProps = {
  material: Material;
  filter: Record<number, number[]>;
  extendFilter: Record<number, number[]>;
  marginTop?: number;
  collects?: Set<string>;
  rawCollects?: Set<number>;
  onlyCollect?: boolean;
  showDecrepted?: boolean;
  onCollect?: (id: number | string, collected: boolean) => void;
};

const index2Combine = (index: number, attrIndexs: number[][]): number[] => {
  var i = index;
  var combine = new Array(attrIndexs.length);
  combine.fill(-1);
  for (var j = attrIndexs.length - 1; j >= 0; j--) {
    var l = attrIndexs[j].length;
    combine[j] = i % l;
    i = Math.floor(i / l);
  }
  return combine;
};

type Combine = {
  id: string;
  combine: number[];
  extendCombine?: number[];
};

export interface ImageListInstance {
  count: () => number;
  filteredCount: () => number;
}

const ImageList = (props: ImageListProps, ref: Ref<ImageListInstance>) => {
  const {
    material,
    filter,
    extendFilter,
    marginTop,
    onlyCollect,
    collects,
    rawCollects,
    onCollect,
    showDecrepted,
  } = props;

  const avatarSize = 272;

  const isExtend = !!material.extendName;

  const rawOnlyCollect = onlyCollect || isExtend;

  const allAttributeIndexs: number[][] = material.attributes.map((attr) =>
    attr.values.map((_, i) => i)
  );

  const allExtendAttributeIndexs: number[][] = material.extendAttributes
    ? material.extendAttributes.map((item) =>
        item.attribute.values.map((_, i) => i)
      )
    : [];

  const filterAttributeIndexs: number[][] = allAttributeIndexs.map(
    (attrValues, index) =>
      filter[index] && filter[index].length > 0 ? filter[index] : attrValues
  );

  const filterExtendAttributeIndexs: number[][] = allExtendAttributeIndexs.map(
    (attrValues, index) =>
      extendFilter[index] && extendFilter[index].length > 0
        ? extendFilter[index]
        : attrValues
  );

  const rawCollectIds = isExtend
    ? useMemo(
        () => Array.from(rawCollects?.values() || []).sort((a, b) => a - b),
        [rawCollects]
      )
    : useMemo(
        () =>
          rawOnlyCollect
            ? Array.from(collects?.values() || [])
                .map((v) => parseInt(v))
                .sort((a, b) => a - b)
            : [],
        [rawOnlyCollect, collects]
      );

  const rawCollectCombines: Combine[] = useMemo(() => {
    return rawCollectIds.map((id) => ({
      id: id.toString(),
      combine: index2Combine(id, allAttributeIndexs),
    }));
  }, [rawCollectIds]);

  const rawCollectFilterCombines: Combine[] = useMemo(() => {
    return rawCollectCombines.filter(({ combine }) =>
      combine.every((v, i) =>
        filter[i] && filter[i].length > 0 ? filter[i].includes(v) : true
      )
    );
  }, [rawCollectCombines, filter]);

  const extendCollectFilterCombines: Combine[] = isExtend
    ? useMemo(() => {
        return Array.from(collects?.values() || []).reduce(
          (prev: Combine[], id: string) => {
            const ids = id.split("-");
            const rawCollectId = ids[0];
            const extendId = parseInt(ids[1]);
            const rawCombine = rawCollectFilterCombines.find(
              ({ id }) => id === rawCollectId
            );
            if (!rawCombine) return prev;
            const extendCombine = index2Combine(
              extendId,
              allExtendAttributeIndexs
            );
            const extendFilted = extendCombine.every((v, i) =>
              extendFilter[i] && extendFilter[i].length > 0 ? extendFilter[i].includes(v) : true);
            if (!extendFilted) return prev;
            return [
              ...prev,
              {
                id,
                combine: rawCombine.combine,
                extendCombine,
              },
            ];
          },
          []
        );
      }, [rawCollectFilterCombines, collects, extendFilter])
    : rawCollectFilterCombines;

  const total = rawOnlyCollect
    ? rawCollectIds.length
    : allAttributeIndexs.reduce((prev, indexs) => prev * indexs.length, 1);
  const filteredTotal = rawOnlyCollect
    ? rawCollectFilterCombines.length
    : filterAttributeIndexs.reduce((prev, indexs) => prev * indexs.length, 1);

  const extendAttrCombineTotal = allExtendAttributeIndexs.reduce(
    (prev, indexs) => prev * indexs.length,
    1
  );
  const filteredExtendAttrCombineTotal = filterExtendAttributeIndexs.reduce(
    (prev, indexs) => prev * indexs.length,
    1
  );

  const extendTotal = onlyCollect
    ? collects?.size || 0
    : extendAttrCombineTotal * rawCollectIds.length;
  const extendFilteredTotal = onlyCollect
    ? extendCollectFilterCombines.length
    : filteredExtendAttrCombineTotal * rawCollectFilterCombines.length;

  const showedTotal = isExtend ? extendFilteredTotal : filteredTotal;

  useImperativeHandle(ref, () => ({
    count: () => (isExtend ? extendTotal : total),
    filteredCount: () => showedTotal,
  }));

  const getIdAndCombine = (index: number): Combine => {
    if (isExtend) {
      if (onlyCollect) {
        return extendCollectFilterCombines[index];
      }

      const combineIndex = isExtend
        ? Math.floor(index / filteredExtendAttrCombineTotal)
        : index;
      const { id, combine } = rawCollectFilterCombines[combineIndex];

      const extendCombineIndex = isExtend
        ? index % filteredExtendAttrCombineTotal
        : 0;
      const [extendId, extendCombine] = calIdAndCombine(
        extendCombineIndex,
        filterExtendAttributeIndexs
      );

      return {
        id: `${id}-${extendId}`,
        combine,
        extendCombine,
      };
    } else {
      if (onlyCollect) {
        return rawCollectFilterCombines[index];
      }

      const [id, combine] = calIdAndCombine(index, filterAttributeIndexs);
      return {
        id: id.toString(),
        combine,
      };
    }
  };

  const calIdAndCombine = (
    index: number,
    attributeIndexs: number[][]
  ): [number, number[]] => {
    const combine = index2Combine(index, attributeIndexs);
    const combineIndex = combine.map(
      (valueIndex, attrIndex) => attributeIndexs[attrIndex][valueIndex]
    );
    const [id, multiple] = [...combineIndex].reverse().reduce(
      ([prev, multiple], v, i) => {
        const m =
          multiple *
          material.attributes[material.attributes.length - i - 1].values.length;
        return [prev + multiple * v, m];
      },
      [0, 1]
    );
    return [id, combineIndex];
  };

  return (
    <>
      <AutoSizer>
        {({ height, width }) => {
          const columnCount = Math.floor(width / avatarSize);
          const rowCount = Math.ceil(showedTotal / columnCount);

          return (
            <FixedSizeGrid
              className="container"
              columnCount={columnCount}
              columnWidth={avatarSize}
              height={height - (marginTop || 0)}
              rowCount={rowCount}
              rowHeight={avatarSize}
              overscanRowCount={4}
              width={width}
            >
              {({ columnIndex, rowIndex, style }) => {
                const index = rowIndex * columnCount + columnIndex;
                if (index >= showedTotal) return null;

                const { id, combine, extendCombine } = getIdAndCombine(index);
                return (
                  <div style={style}>
                    <Avatar
                      key={index}
                      id={id}
                      collected={collects && collects.has(id)}
                      onCollectedChange={(collected) =>
                        onCollect && onCollect(id, collected)
                      }
                      decrepted={
                        showDecrepted &&
                        combine.every(
                          (valueIndex, attrIndex) =>
                            material.attributes[attrIndex].values[valueIndex]
                              .isOld
                        )
                      }
                      attrs={combine.map(
                        (valueIndex, attrIndex) =>
                          material.attributes[attrIndex].values[valueIndex].img
                      )}
                      extendAttrs={
                        material.extendAttributes && extendCombine
                          ? extendCombine.map((valueIndex, attrIndex) => ({
                              stack:
                                material.extendAttributes?.[attrIndex].stack ||
                                0,
                              attr:
                                material.extendAttributes?.[attrIndex].attribute
                                  .values[valueIndex].img || "",
                            }))
                          : []
                      }
                    />
                  </div>
                );
              }}
            </FixedSizeGrid>
          );
        }}
      </AutoSizer>
    </>
  );
};

export default forwardRef(ImageList) as (
  props: ImageListProps & { ref?: Ref<ImageListInstance> }
) => JSX.Element;
