import React, { useEffect, useMemo, useState } from "react";
import Checkbox from "@mui/material/Checkbox";
import Select, { SelectProps } from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";

type Option = {
  label: string;
  value: number;
}

type MultipleSelectProps = {
  label: string;
  options: Option[];
  value?: number[];
  onChange?: (value: number[]) => void;
} & Omit<SelectProps, "value" | "onChange">;

const truncateString = (str: string, length: number) => {
  if (str.length > length) {
    return str.slice(0, length) + "...";
  } else {
    return str;
  }
}

const MultipleSelect = (props: MultipleSelectProps) => {
  const { options, label, value, onChange, ...rest } = props;
  const [selected, setSelected] = useState<number[]>([]);
  const isAllSelected = options.length > 0 && selected.length === options.length;

  useEffect(() => {
    setSelected(value ? value : []);
  }, [value])

  const handleChange = (e: any) => {
    let value = e.target.value;
    if (value[value.length -1] === -1) {
      value = selected.length === options.length ? [] : options.map(o => o.value);
    }
    // TODO support multiple select
    onChange ? onChange(value) : setSelected(value);
  }

  const labelMap: Record<number, string> = useMemo(() => options.reduce((prev, cur) => ({
    ...prev,
    [cur.value]: cur.label,
  }), {}), [options]);

  return (
    <Select
      autoWidth
      size="small"
      multiple
      onChange={handleChange}
      value={selected}
      displayEmpty
      renderValue={value => {
        return (value.length === options.length || value.length === 0) ? 
          `全部${label}` : 
          truncateString(value.map(v => labelMap[v]).join(","), 8);
      }}
    >
      <MenuItem value={-1}>
        <ListItemIcon>
          <Checkbox checked={isAllSelected} indeterminate={selected.length > 0 && selected.length < options.length} />
        </ListItemIcon>
        <ListItemText primary= {`全部${label}`} />
      </MenuItem>
      {options.map((option, i) => (
        <MenuItem key={i} value={option.value}>
          <ListItemIcon>
            <Checkbox checked={selected.includes(option.value)} />
          </ListItemIcon>
          <ListItemText primary={option.label} />
        </MenuItem>
      ))}
    </Select>
  )
}

export default MultipleSelect;